.nbp-slider-storico-popover {

    // width:80px;
    // position: absolute;
    // bottom: 100%;
    // margin-bottom: 27px;
    // margin-left: 9px;
    // left: 50%;
    // transform: translate(-50%, 0);

    width:80px;
    position: absolute;
    bottom: 100%;
    margin-bottom: 31px;
    left: 50%;
    margin-left:10px;
    transform: translate(-50%, 0);
    
    .nbp-slider-storico-popover-content {
        background-color: #F9F9F9;
        border: 1px solid #FFF;
        padding: 8px 12px;
        border-radius: 3px;
        box-shadow: 0 1px 1px #CCC;


        &:after, &:before {
            top: 100%;
            left: 50%;
            border: solid transparent;
            content: " ";
            height: 0;
            width: 0;
            position: absolute;
            pointer-events: none;
        }

        &:after {
            border-color: rgba(136, 183, 213, 0);
            border-top-color: #FFF;
            border-width: 6px;
            margin-left: -6px;
        }
        &:before {
            border-color: rgba(194, 225, 245, 0);
            border-top-color: #DDD;
            border-width: 7px;
            margin-left: -7px;
        }
    }
}